@charset "UTF-8";
@font-face {
  font-family: "Lato-Bold";
  src: url("../fonts/Lato-Bold.woff2") format("woff2"), url("../Lato-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato-Regular";
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato-SemiBold";
  src: url("../fonts/Lato-SemiBold.woff2") format("woff2"), url("../Lato-SemiBold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Raleway-ExtraBold";
  src: url("../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../Raleway-ExtraBold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  position: relative;
  z-index: -1;
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: var(--font-family-main-400);
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  line-height: 135%;
  text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

button {
  cursor: pointer;
}

a,
a:visited {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

:root {
  --white-color: #FFFFFF;
  --black-color: #000000;
  --vulcan: #10101D;
  --picton-blue: #42A9ED;
  --picton-blue-2: #43ABF0;
  --havelock-blue: #3E9CDC;
  --storm-gray: #6F6D7B;
  --shark: #2A2B30;
  --font-family-main-400: "Lato-Regular", Helvetica, Arial, sans-serif;
  --font-family-main-600: "Lato-SemiBold", Helvetica, Arial, sans-serif;
  --font-family-main-700: "Lato-Bold", Helvetica, Arial, sans-serif;
  --font-family-secondary-800: "Raleway-ExtraBold", "Helvetica Neue", "Arial Black", Arial, sans-serif;
  --border-radius-small: 4px;
  --transition-main: cubic-bezier(0.767, 0.01, 0.18, 1.01);
}

.body--blocked {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  border-radius: var(--border-radius-small);
  padding: 5px 10px;
  font-family: var(--font-family-main-600);
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
  background-color: transparent;
}
.button--small {
  min-width: 183px;
  min-height: 43px;
  font-family: var(--font-family-main-400);
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}
.button--blue {
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
  background-color: #3E9CDC;
}
.button--white {
  border: 1px solid #6F6D7B;
  color: #2A2B30;
  background-color: #FFFFFF;
}
@media (min-width: 1023.98px) and (hover) {
  .button {
    transition: border 0.2s var(--transition-main), color 0.2s var(--transition-main), background-color 0.2s var(--transition-main);
  }
  .button--blue:hover {
    background-color: #42A9ED;
  }
  .button--white:hover {
    border: 1px solid #42A9ED;
    color: #42A9ED;
  }
}

.burger {
  position: relative;
  z-index: 10;
  outline: 0;
  border: none;
  width: 40px;
  height: 30px;
  background: transparent;
}

.burger:focus {
  outline: 0;
}

.burger span:nth-child(2) {
  position: absolute;
  top: 14px;
  left: 50%;
  transform: translateX(-50%);
  width: 30px;
  height: 2px;
  background-color: #FFFFFF;
  transition: width 0.3s linear;
}

.burger span:nth-child(1) {
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 2px;
  background-color: #FFFFFF;
  transition: top 0.3s 0.3s, transform 0.3s linear;
  transform-origin: 50% 50%;
}

.burger span:nth-child(3) {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 2px;
  background-color: #FFFFFF;
  transition: top 0.3s 0.3s, transform 0.3s linear;
  transform-origin: 50% 50%;
}

.menu--active .burger span:nth-child(2) {
  width: 0;
  transition: width 0.3s linear;
}

.menu--active .burger span:nth-child(1) {
  top: 14px;
  transform: translateX(-50%) rotate(-45deg);
  transition: top 0.3s, transform 0.3s 0.3s linear;
}

.menu--active .burger span:nth-child(3) {
  top: 14px;
  transform: translateX(-50%) rotate(45deg);
  transition: top 0.3s, transform 0.3s 0.3s linear;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.list--dash li {
  position: relative;
  margin-bottom: 5px;
  padding-left: 25px;
  font-family: var(--font-family-main-400);
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}
.list--dash li::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 11px;
  width: 5px;
  height: 2px;
  background-color: #000000;
}
.list--dash li:last-child {
  margin-bottom: 0;
}

.container {
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: rgba(16, 16, 29, 0.9);
  transition: height 0.2s var(--transition-main);
}
.header.header--compressed {
  height: 70px;
}

.header__container {
  display: flex;
  align-items: center;
  height: 100%;
}

.header__logo {
  display: block;
  margin-right: auto;
  font-family: var(--font-family-secondary-800);
  font-weight: 800;
  font-size: 28px;
  line-height: 33px;
  color: #FFFFFF;
}
.header__logo span {
  color: #42A9ED;
}

.header__menu {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.header__link {
  display: inline-block;
  font-family: var(--font-family-main-600);
  font-weight: 600;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  color: #FFFFFF;
  cursor: pointer;
}

.header__burger {
  display: none;
}

@media (min-width: 1023.98px) and (hover) {
  .header__link {
    transition: color 0.2s var(--transition-main);
  }
  .header__link:hover {
    color: #43ABF0;
  }
}
@media (min-width: 899.98px) {
  .header__item + .header__item {
    margin-left: 60px;
  }
}
@media (min-width: 767.98px) and (max-width: 899.98px) {
  .header__item + .header__item {
    margin-left: 40px;
  }
}
@media (max-width: 767.98px) {
  .header {
    position: relative;
    height: 85px;
  }
  .header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 11;
    width: 100%;
    height: 100%;
    background-color: rgba(16, 16, 29, 0);
    transition: background-color 0.3s var(--transition-main) 0.1s;
  }
  .header__logo {
    z-index: 12;
  }
  .header__nav {
    position: fixed;
    top: -100%;
    left: 0;
    z-index: 0;
    overflow-y: auto;
    padding: 100px 60px 40px;
    width: 100%;
    height: 100%;
    background-color: transparent;
    transition-duration: 0s;
    transition-delay: 0.4s;
  }
  .header__nav::before, .header__nav::after {
    content: "";
    position: fixed;
    top: 0;
    width: 51%;
    height: 100%;
    background-color: #10101d;
  }
  .header__nav::before {
    left: -52%;
    transition: left 0.3s ease-in 0.3s;
  }
  .header__nav::after {
    right: -52%;
    transition: right 0.3s ease-in 0.3s;
  }
  .header__menu {
    position: relative;
    z-index: 1;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .header__item + .header__item {
    margin-top: 40px;
  }
  .header__link {
    position: relative;
    opacity: 0;
    transition: opacity 0.35s cubic-bezier(0.767, 0.01, 0.18, 1.01);
  }
  /* Из активного в неактивное состояние */
  .header__item:nth-child(1) .header__link {
    transition-duration: 0.2s;
    transition-delay: 0.14s;
  }
  .header__item:nth-child(2) .header__link {
    transition-duration: 0.2s;
    transition-delay: 0.1s;
  }
  .header__item:nth-child(3) .header__link {
    transition-duration: 0.2s;
    transition-delay: 0.06s;
  }
  .header__item:nth-child(4) .header__link {
    transition-duration: 0.2s;
    transition-delay: 0.02s;
  }
  /* Из неактивного в активное состояние */
  .menu--active .header__item:nth-child(1) .header__link {
    transition-delay: 0.3s;
  }
  .menu--active .header__item:nth-child(2) .header__link {
    transition-delay: 0.35s;
  }
  .menu--active .header__item:nth-child(3) .header__link {
    transition-delay: 0.4s;
  }
  .menu--active .header__item:nth-child(4) .header__link {
    transition-delay: 0.45s;
  }
  .header__burger {
    z-index: 11;
    display: block;
  }
  /* Бургер меню открыто */
  .menu--active {
    /* Левая створка "закрыта" */
    /* Правая створка "закрыта" */
  }
  .menu--active.header::before {
    background-color: #10101d;
  }
  .menu--active .header__nav {
    top: 0;
    transition-delay: 0.4s;
  }
  .menu--active .header__nav::before {
    left: 0;
    transition-delay: 0s;
  }
  .menu--active .header__nav::after {
    right: 0;
    transition-delay: 0s;
  }
  .menu--active .header__link {
    opacity: 1;
  }
}
@media (max-width: 549.98px) {
  .header {
    height: 70px;
  }
  .header.header--compressed {
    height: 60px;
  }
  .header__logo {
    font-size: 24px;
    line-height: 29px;
  }
}
@media (max-width: 419.98px) {
  .header {
    height: 60px;
  }
  .header.header--compressed {
    height: 50px;
  }
  .header__logo {
    font-size: 22px;
    line-height: 27px;
  }
  .header__nav {
    padding: 85px 45px;
  }
}
.hero {
  padding: 200px 0;
  min-height: 850px;
  background-color: #F7F9FB;
  background-image: url("../../images/hero/bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__title {
  margin: 0;
  margin-bottom: 80px;
  font-family: var(--font-family-main-700);
  font-weight: 700;
  font-size: 48px;
  line-height: 58px;
  text-transform: uppercase;
}
.hero__title span {
  color: #3E9CDC;
}

.hero__paragraph {
  margin: 0;
  margin-bottom: 10px;
  font-family: var(--font-family-main-400);
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}
.hero__paragraph--bold {
  font-family: var(--font-family-main-700);
  font-weight: 700;
}

.hero__list {
  margin-bottom: 80px;
}

.hero__actions {
  display: flex;
}

.hero__button + .hero__button {
  margin-left: 30px;
}

@media (min-width: 767.98px) and (max-width: 991.98px) {
  .hero {
    background-position: 63% 50%;
  }
}
@media (max-width: 767.98px) {
  .hero__title br {
    display: none;
  }
}
@media (min-width: 479.98px) and (max-width: 767.98px) {
  .hero {
    background-position: 5% 50%;
  }
}